.container {
  width: 1200px;
  margin: 0 auto;
  background-color: $white;
  box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
}

.obertka {
  width: 400px;
  margin: 47px 55px 40px;

  h2 {
    font-size: 21px;
    padding: 0 40px;
    margin-bottom: 23px;
  }

  p {
    margin-top: 22px;
    padding: 0;
  }
}

.form-obertka {
  display: flex;
  flex-direction: column;

  p:first-child {
    label {
      margin-right: 40px;
    }

    .btn-plus {
      margin-right: 51px;
    }
  }

  p:last-child {
    label {
      margin-right: 27px;
    }
  }
}
